<template>
    <div id="app" class="main-container" :class="{'sidebar-open': isActive }">
        <Navbar/>
        <Sidebar/>
        <b-container>
            <router-view/>
        </b-container>
    </div>
</template>

<style lang="scss">
    @import "./assets/scss/style";
</style>

<script>
    import Navbar from '@/components/Navbar.vue'
    import Sidebar from '@/components/Sidebar.vue'
    import SecondSidebar from '@/components/SecondSidebar.vue'

    export default {
        name: 'app-header',
        components: {Navbar, Sidebar, SecondSidebar},
        data() {
            return {
                isActive: false
            }
        },
        created() {
            this.$root.$on('collapseSidebar', () => {
                this.isActive = !this.isActive;
            })
        }
    }
</script>
